  <div class="recordViewContainer">
      <div class="chat">
        <div class="chatHeader">
          <p style="font-size:16px;">對話記錄</p>
          <a v-bind:href="this.ccbotUrl + this.accessParam" target="_blank">
            <input type="button" v-on:click="getEnterCCbotUrl" class="search-button" value="查詢"/>
          </a>
        </div>
        <div class="chatContent">
          <!-- <div class="task-btn" v-on:click="viewRecordDetail({ data },{ data })"> -->
          <!-- <div class="task-btn" v-on:click="testAxios()">click here</div> -->
          <div v-for="message in currentRecordData" :key="message.id">
