<script>
// import { createNamespacedHelpers } from 'vuex'
import api from '@/api/api'
import cryptojs from 'crypto-js'

// const { mapMutations, mapState } = createNamespacedHelpers('callCenter/record')
// const GET_RECORD_DETAIL_URL = 'http://192.168.3.242/ccbot/aicc/api/cc/v1/chat_records'

export default {
  name: 'record-detail',
  // computed: {
  //   ...mapState(['currentRecordData'])
  // },
  data () {
    return {
      record: {},
      data: 'dataTest',
      currentRecordData: {},
      currentTag: {},
      ccbotUrl: '',
      accessParam: ''
    }
  },
  // beforeMount() {},
  mounted () {
    // window get param url
    this.getRecordDetail(this.$route.query.callID)
    this.getTag(this.$route.query.callID)
    this.getEnterCCbotUrl(this.$route.query.callID)
  },
  methods: {
    getEnterCCbotUrl (callID) {
      api.getEnterCCbotUrl(callID).then((response) => {
        console.log(response)
        this.ccbotUrl = response
      })
    },
    getRecordDetail (callID) {
      console.log('axios execute:', callID)
      api.getRecordDetail3(callID).then((response) => {
        this.currentRecordData = response.result.data.reverse()
      })
    },
    getTag (callID) {
      console.log('axios execute:', callID)
      api.getTag(callID).then((response) => {
        const sourceData = response.result.data[0]
        console.log(sourceData)
        this.currentTag = sourceData.extend_data
        delete sourceData.extend_data
        for (const [key, value] of Object.entries(sourceData)) {
          this.currentTag[key] = value
        }
        console.log(sourceData)
        console.log(this.currentTag)

        // this.accessParam = 'pid=' + this.currentTag['*PID'] + '&campa_id=' + this.currentTag['*CampaID']
        let ts = Math.round(new Date() / 1000).toString()
        const base64 = this.encryptAES_ECB(this.currentTag['*PID'], ts)
        this.accessParam = 'pid=' + base64 + '&campa_id=' + this.currentTag['*CampaID'] + '&ts=' + ts

        console.log(this.accessParam)
      })
    },
    encryptAES_ECB (msg, keyStr) {
      const src = cryptojs.enc.Utf8.parse(msg)
      const keySHA = cryptojs.SHA256(keyStr).toString(cryptojs.enc.Hex).substr(0, 16)
      const key = cryptojs.enc.Utf8.parse(keySHA)
      const enc = cryptojs.AES.encrypt(src, key, {
        mode: cryptojs.mode.ECB,
        padding: cryptojs.pad.Pkcs7
      })

      let base64 = enc.toString().replace(/\+/g, '-')
      base64 = base64.replace(/\//g, '_')

      return base64
    }
  }
}
</script>

