import Vue from 'vue'
import Router from 'vue-router'
import chatList from '@/components/chatList'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'chatList',
      component: chatList
    }
  ]
})



